.feature-card {
  background-color: #ffffff;
  border-radius: 12px;
  padding: 25px;
  width: 300px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.08);
  transition: transform 0.3s;
}

.feature-card:hover {
  transform: translateY(-8px);
}
.feature-card h3 {
  font-size: 20px;
  margin-bottom: 10px;
}